
.display-area {
  background-color: RGBA(153, 153, 153, 0.8);
  color: #fff;
  height: 1rem;
  text-align: right;
  padding: 0 0.1rem;
}
.display-area .current {
    font-size: 0.23rem;
    height: 60%;
    line-height: 0.6rem;
    border-bottom: 1px solid black;
}
.display-area .expression {
    font-size: 0.18rem;
    height: 40%;
    line-height: 0.4rem;
}
table {
  border-collapse: collapse;
  width: 100%;
  height: 3rem;
}
td {
  border: 1px solid RGBA(142, 142, 142, 1);
  width: 25%;
  text-align: center;
  font-size: 0.26rem;
  background-color: RGBA(224, 224, 224, 1);
  transition: 200ms;
}
td:hover {
    transform: scale(1.015);
}
.danger {
  background-color: RGBA(238, 66, 87, 1);
  color: #fff;
}
.operator {
  background-color: RGBA(245, 146, 62, 1);
  color: #fff;
}
.disable-select {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}



